<template>
	<view>
		
	</view>
</template>

<script>
	export default {
		data() {
			return {
					user_en:"",
					user_id:""
			}
		},
			// 注意获取参数是要在上面生成二维码的页面 我上面写的是 pages/user/user , 下面的代码也是在 pages/user/user 里面的onLoad中
			onLoad(e) {
				console.log("e")
				console.log(e)
				
				// 获取二维码带过来的参数,或者内嵌扫一扫跳转过来的参数
				this.user_en = decodeURIComponent(e.scene).split('&')[0]
				this.user_id = decodeURIComponent(e.scene).split('&')[1].split('=')[1]
			},
		methods: {
			scan() {
					let that = this
					//访问扫描API
					uni.scanCode({
						// 扫描成功
						success: res => {
							// 将参数解码
							let path=decodeURIComponent(res.path)
							// 直接进行跳转
							uni.navigateTo({
								// 我上面的二维码地址是 pages/user/user, 扫码后会跳转到 pages/user/user并且会携带参数在后面
								url: '/' + decodeURIComponent(res.path)
							})
						},
						// 扫码失败
						fail: (res) => {
							uni.showToast({
								title: "扫码失败",
								icon: 'error',
								duration: 1000
							})
						}
					})
				}
		}
	}
</script>

